import React, { useCallback, useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Checkbox } from "primereact/checkbox";
import { Dropdown } from "primereact/dropdown";
import { InputSwitch } from "primereact/inputswitch";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { currencyActions } from "../../store/currency/currencySlice";
import { userActions } from "../../store/user/userSlice";
import { roleActions } from "../../store/role/roleSlice";
import { IAppState } from "../../models/appState";
import { IUser } from "../../models/user";
import { emailRegex } from "../../utilities/helpers";
import {
  ERROR_MESSAGE_BAD,
  ERROR_MESSAGE_CONFIRM_PASSWORD,
  ERROR_MESSAGE_EMAIL,
  ERROR_MESSAGE_PASSWORD_REENTER,
} from "../../utilities/constants";

const UserEdit: React.FC = () => {
  const defaultItem: IUser = {
    id: "",
    firstName: "",
    lastName: "",
    isAdmin: false,
    description: "",
    title: "",
    department: "",
    phoneWork: "",
    phoneHome: "",
    email: "",
    status: false,
    roles: [],
    permissions: [],
    currencyId: "",
    uid: "",
  };
  const navigateTo = "/users";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if any form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const [password, setPassword] = useState({
    password: "",
    confirmPassword: "",
  });
  const params = useParams();
  const memo = useRef(true);
  const create = useRef(false);
  const update = useRef(false);
  const [user, isFetching, isError, currencies, roles] = useAppSelector(
    (state: IAppState) => [
      state.users.user,
      state.users.isFetching,
      state.users.isError,
      state.currencies.currencies,
      state.roles.roles,
    ],
    shallowEqual
  );
  const dispatch = useAppDispatch();

  const isValidEmail = useCallback((email = "") => emailRegex.test(email), []);

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      dispatch(currencyActions.getCurrencies());
      dispatch(roleActions.getRoles());
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(userActions.getUserById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== user?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(user);
    }
  }, [params, item, user, isFetching, isError]);

  useEffect(() => {
    if (!dirty) {
      return;
    }

    let isValid = true;
    const filter = [
      "id",
      "uid",
      "description",
      "title",
      "department",
      "phoneWork",
      "phoneHome",
    ];
    for (const [key, value] of Object.entries(item)) {
      if (filter.indexOf(key) > -1) {
        continue;
      }

      if (typeof value === "string") {
        if (value?.trim().length === 0) {
          isValid = false;
          break;
        }
      }
    }

    if (!item.uid) {
      isValid =
        password.password.length > 5 &&
        password.password === password.confirmPassword;
    }

    setValid(isValid);
  }, [dirty, item, password]);

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    setItem({ ...item, [tname]: tvalue });
    if (tname === "email") {
      const isEmailValid = isValidEmail(tvalue?.trim());
      setErrors({
        ...errors,
        emailInvalid: !isEmailValid,
        [tname]: tvalue?.trim().length === 0,
      });
    } else if (tname !== "status" && tname !== "isAdmin") {
      setErrors({
        ...errors,
        [tname]: tvalue?.trim().length === 0,
      });
    }
  };

  const onPasswordChange = (e: any) => {
    const {
      target: { name: tname, value: tvalue },
    } = e;

    setPassword({ ...password, [tname]: tvalue });
    if (tname === "confirmPassword") {
      setErrors({
        ...errors,
        confirmPasswordEmpty: (tvalue as string)?.trim().length === 0,
        confirmPasswordReenter:
          (tvalue as string)?.trim() !== password.password,
      });
    } else if (tname === "password") {
      setErrors({
        ...errors,
        weakPassword: (tvalue as string)?.trim().length < 6,
      });
    }
  };

  const getPermissions = (items: string[]) => {
    const permissions: string[] = [];
    items.forEach((roleName) => {
      const role = roles.find((roleItem) => roleItem.name === roleName);
      if (role && role.permissions) {
        role.permissions.forEach((permissionItem) => {
          if (permissions.indexOf(permissionItem) < 0) {
            permissions.push(permissionItem);
          }
        });
      }
    });

    return permissions;
  };

  const onRoleChange = (e: any) => {
    setDirty(true);
    const items = [...(item.roles || [])];
    if (e.checked) {
      items.push(e.value);
    } else {
      items.splice(items.indexOf(e.value), 1);
    }

    const permissions = getPermissions(items);
    setItem({ ...item, roles: items, permissions });
  };

  const mapRoles = () => {
    return roles.map((value) => (
      <li key={value.id}>
        <Checkbox
          name="role"
          inputId={value.id}
          value={value.name}
          onChange={onRoleChange}
          checked={(item.roles || []).some((data) => data === value?.name)}
        />
        <label htmlFor={value?.name} className="p-checkbox-label">
          {value?.name}
        </label>
      </li>
    ));
  };

  const onSaveClick = async () => {
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">User: {subtitle}</h3>
      <div className="grid">
        {(create.current || update.current) && isError && (
          <div className="col">
            <Message severity="error" text={ERROR_MESSAGE_BAD} />
          </div>
        )}
        <div className="col">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>User</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="FirstName">
                  First Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="FirstName"
                  name="firstName"
                  maxLength={30}
                  placeholder="Joe"
                  value={item?.firstName}
                  onChange={onChange}
                />
                {(errors as any)?.firstName && (
                  <Message severity="error" text="First Name is required." />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="LastName">
                  Last Name
                  <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="LastName"
                  name="lastName"
                  maxLength={30}
                  placeholder="Doe"
                  value={item.lastName}
                  onChange={onChange}
                />
                {(errors as any)?.lastName && (
                  <Message severity="error" text="Last Name is required." />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Email">
                  Email <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="Email"
                  name="email"
                  maxLength={255}
                  placeholder="Example jdoe@example.com"
                  value={item.email}
                  disabled={params.mode === "edit"}
                  onChange={onChange}
                />
                {(errors as any)?.email && (
                  <Message severity="error" text="Email is required." />
                )}
                {(errors as any)?.emailInvalid && (
                  <Message severity="error" text={ERROR_MESSAGE_EMAIL} />
                )}
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Currency">
                  Currency <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <Dropdown
                  id="Currency"
                  name="currencyId"
                  options={currencies}
                  optionLabel="name"
                  optionValue="id"
                  value={item.currencyId}
                  onChange={onChange}
                  className="p-combobox"
                />
                {(errors as any)?.currencyId && (
                  <Message severity="error" text="Currency is required." />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PhoneWork">Office Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneWork"
                  name="phoneWork"
                  maxLength={50}
                  value={item?.phoneWork}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="PhoneHome">Home Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneHome"
                  name="phoneHome"
                  maxLength={50}
                  value={item.phoneHome}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Title">Title</label>
              </div>
              <div className="col">
                <InputText
                  id="Title"
                  name="title"
                  maxLength={50}
                  value={item?.title}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Department">Department</label>
              </div>
              <div className="col">
                <InputText
                  id="Department"
                  name="department"
                  maxLength={50}
                  value={item.department}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label>Status</label>
              </div>
              <div className="col">
                <div className="p-fixed-switch">
                  <InputSwitch
                    name="status"
                    checked={item.status || false}
                    value="0"
                    onChange={onChange}
                  />
                </div>
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label>Admin</label>
              </div>
              <div className="col">
                <div className="p-fixed-switch">
                  <InputSwitch
                    name="isAdmin"
                    checked={item.isAdmin || false}
                    value="0"
                    onChange={onChange}
                  />
                </div>
              </div>
            </div>
          </div>
          {params.mode === "add" && (
            <div className="col-12">
              <div className="grid">
                <div className="col">
                  <label htmlFor="Password">
                    Password <span className="field-required">*</span>
                  </label>
                </div>
                <div className="col">
                  <InputText
                    id="Password"
                    name="password"
                    type="password"
                    maxLength={50}
                    value={password?.password}
                    onChange={onPasswordChange}
                  />
                  {(errors as any)?.weakPassword && (
                    <Message
                      severity="error"
                      text="Password should be at least 6 characters."
                    />
                  )}
                </div>
                <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
                <div className="col">
                  <label htmlFor="ConfirmPassword">
                    Confirm Password <span className="field-required">*</span>
                  </label>
                </div>
                <div className="col">
                  <InputText
                    id="ConfirmPassword"
                    name="confirmPassword"
                    type="password"
                    maxLength={50}
                    value={password?.confirmPassword}
                    onChange={onPasswordChange}
                  />
                  {(errors as any)?.confirmPasswordEmpty && (
                    <Message
                      severity="error"
                      text={ERROR_MESSAGE_CONFIRM_PASSWORD}
                    />
                  )}
                  {(errors as any)?.confirmPasswordReenter && (
                    <Message
                      severity="error"
                      text={ERROR_MESSAGE_PASSWORD_REENTER}
                    />
                  )}
                </div>
              </div>
            </div>
          )}
          <div className="col-12">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="UserRoles">User Roles</label>
              </div>
              <div className="col-12">
                <ul
                  id="UserRoles"
                  style={{
                    listStyleType: "none",
                    marginTop: 0,
                    paddingLeft: 0,
                  }}
                >
                  {mapRoles()}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Notes</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <InputTextarea
              name="description"
              value={item.description}
              onChange={onChange}
              autoResize
            />
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default UserEdit;
